.divPedido{
    max-width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.divLinha{
    border: 1px solid black;
    width: 99.9%;
}



.divTipos{
    width: 50%;
    display: flex;
    justify-content: space-between;
}

.divTipos button{
    display: inline-block;
    padding: 15px 20px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #04AA6D;
    border: none;
    border-radius: 15px;
    box-shadow: 0 9px #999;
  }

  .divTipos button:hover{
    background-color: #3e8e41
  }

  .divTipos button:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }
  .divMenu{
    width: 100%;
    display: none;
  }

  @media (max-width: 600px){
    .divMenu{
        display: block;
    }
    .divTipos{
        display: none;
    }
  }

  .divMenu button{
    display: inline-block;
    padding: 15px 15px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #04AA6D;
    border: none;
    border-radius: 15px;
    box-shadow: 0 10px #999;
  }

  .divMenu button:hover{
    background-color: #3e8e41
  }

  .divMenu button:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(5px);
  }